@keyframes addClone {
	0% {
		opacity: 0;
		transform: scale(0);
	}
	100% {
		opacity: 1;
		transform: scale(0.8);
	}
}

@keyframes addToBacket {
	0% {
		opacity: 1;
		transform: scale(0.8);
		width: 150px;
		height: 150px;
	}
	25% {
		opacity: 1;
		transform: scale(0.8);
		width: 150px;
		height: 150px;
	}
	50% {
		opacity: 1;
		transform: scale(0.8);
	}
	100% {
		opacity: 0;
		transform: scale(0);
		width: 0;
		height: 0;
	}
}

@keyframes showBlock {
	0% {
		max-height: 0;
	}
	100% {
		max-height: 100%;
		min-height: 100%;
	}
}
